import React from "react";
import './header.css'
import ReactTooltip from 'react-tooltip';
import mail_light from './assets/mail_light.svg'
import mail_dark from './assets/mail_dark.svg'
import git_light from './assets/github_light.svg'
import git_dark from './assets/github_dark.svg'
import linkedin_light from './assets/linkedin_light.svg'
import linkedin_dark from './assets/linkedin_dark.svg'
import resume_light from './assets/resume_light.svg'
import resume_dark from './assets/resume_dark.svg'
import resume_file from './assets/resume.pdf'
import ButtonMailto from './ButtonMalito'

export default function Header({backgroundC}) {
    let mainStyle = backgroundC.animation.to || '#f0f0f0'
    return(
        <div className={mainStyle === '#f0f0f0' ? 'header' : 'header-dark'}>
            <div className='icons'>
                <div className={mainStyle === '#f0f0f0' ? 'mail' : 'mail-dark'} data-tip="Mail">
                    <ButtonMailto email='vptilwani@gmail.com' subject='Hello'>
                    <img src = {mail_light} alt="Mail SVG" className={mainStyle === '#f0f0f0' ? '' : 'hide'}/>
                    <img src = {mail_dark} alt="Mail SVG" className={mainStyle === '#f0f0f0' ? 'hide' : ''}/>
                    </ButtonMailto>
                    <ReactTooltip place="right" type="info" effect="solid"/>
                </div>
                <div className={mainStyle === '#f0f0f0' ? 'linkedin' : 'linkedin-dark'} data-tip="LinkedIn">
                    <a href='https://linkedin.com/in/vedant-tilwani-creative-developer' target="_blank">
                    <img src = {linkedin_light} alt="LinkedIn SVG" className={mainStyle === '#f0f0f0' ? '' : 'hide'}/>
                    <img src = {linkedin_dark} alt="LinkedIn SVG" className={mainStyle === '#f0f0f0' ? 'hide' : ''}/>
                    </a>
                    <ReactTooltip place="right" type="info" effect="solid"/>
                </div>
                <div className={mainStyle === '#f0f0f0' ? 'github' : 'github-dark'} data-tip="GitHub">
                    <a href='https://github.com/TheNoblesse05' target="_blank">
                    <img src = {git_light} alt="GitHub SVG" className={mainStyle === '#f0f0f0' ? '' : 'hide'}/>
                    <img src = {git_dark} alt="GitHub SVG" className={mainStyle === '#f0f0f0' ? 'hide' : ''}/>
                    </a>
                    <ReactTooltip place="right" type="info" effect="solid"/>
                </div>
                <div className={mainStyle === '#f0f0f0' ? 'resume' : 'resume-dark'} data-tip="Resume">
                    <a href={resume_file} download='Vedant Tilwani Resume'>
                    <img src = {resume_light} alt="Resume SVG" className={mainStyle === '#f0f0f0' ? '' : 'hide'}/>
                    <img src = {resume_dark} alt="Resume SVG" className={mainStyle === '#f0f0f0' ? 'hide' : ''}/>
                    </a>
                    <ReactTooltip place="right" type="info" effect="solid"/>
                </div>
            </div>
            <div className={mainStyle === '#f0f0f0' ? 'border-line' : 'border-line-dark'}></div>
        </div>
    )
}