import * as THREE from 'three'
import { useRef, useState, useMemo, useEffect, Suspense } from 'react'
import { Canvas, useFrame } from '@react-three/fiber'
import { Text, TrackballControls, OrbitControls } from '@react-three/drei'
import './aboutme.css'

function Word({ children, ...props }) {
    const color = new THREE.Color()
    const fontProps = { font: '/Inter-Bold.woff', fontSize: 2.5, letterSpacing: -0.05, lineHeight: 1, 'material-toneMapped': false }
    const ref = useRef()
    // const [hovered, setHovered] = useState(false)
    // const over = (e) => (e.stopPropagation(), setHovered(true))
    // const out = () => setHovered(false)
    // Change the mouse cursor on hover
    // useEffect(() => {
    //   if (hovered) document.body.style.cursor = 'pointer'
    //   return () => (document.body.style.cursor = 'auto')
    // }, [hovered])
    // Tie component to the render-loop
    useFrame(({ camera }) => {
      // Make text face the camera
      ref.current.quaternion.copy(camera.quaternion)
      // Animate font color
    //   ref.current.material.color.lerp(color.set(hovered ? '#fa2720' : 'white'), 0.1)
    })
    return <Text ref={ref} {...props} {...fontProps} children={children} />
  }
  
  function Cloud({ count = 4, radius = 20 }) {
    // Create a count x count random words with spherical distribution
    const words = useMemo(() => {
      const temp = []
      const spherical = new THREE.Spherical()
      const phiSpan = Math.PI / (count + 1)
      const thetaSpan = (Math.PI * 2) / count
      const skills = [
        [],
        ['Angular','C','SQL'],
        ['React','Python','Elasticsearch'],
        ['Typescript','Reinforcement Learning','Unity'],
      ]
      for (let i = 1; i < count + 1; i++)
        // Taken from https://discourse.threejs.org/t/can-i-place-obects-on-a-sphere-surface-evenly/4773/6
        for (let j = 0; j < count; j++) temp.push([new THREE.Vector3().setFromSpherical(spherical.set(radius, phiSpan * i, thetaSpan * j)), skills[i][j]])
      return temp
    }, [count, radius])
    return words.map(([pos, word], index) => <Word key={index} position={pos} children={word} />)
  }

function SetFog(backgroundColor) {
    if(backgroundColor.backgroundColor == '#f0f0f0')
        return(<fog attach="fog" args={['#202025', 80, 0]} />)
    if(backgroundColor.backgroundColor == '#202020')
        return(<fog attach="fog" args={['#202025', 0, 80]} />)
    return(<fog attach="fog" args={['#202025', 80, 0]} />)
}

const mystyle = {
    width: '100%',
    height: '100%',
    marginTop: '25vh',
    marginLeft: '10vw',
    cursor: 'grab'
}

export default function AboutMe({backgroundC}) {
    let mainStyle = backgroundC.animation.to || '#f0f0f0'
    return(
        <div className='aboutme-main-container'>
            <div className={ mainStyle === '#f0f0f0' ? 'aboutme-text' : 'aboutme-text-dark' }>
                <div className='aboutme-title'>
                    About Me
                </div>
                <div className='aboutme-desc'>
                    Hi! I'm Vedant Tilwani, a Fullstack Developer based in Bengaluru who's passionate about creating digital experiences. I'm all about combining visual design with product thinking to bring meaningful experiences live.
                </div>
            </div>
            <div className='skill-tree'>
                <Suspense fallback={null}>
                    <Canvas style={mystyle} dpr={[1, 2]} camera={{ position: [0, 0, 35], fov: 90 }}>
                        <SetFog backgroundColor={backgroundC.animation.to} />
                        
                        <Cloud count={3} radius={10} />
                        
                        <OrbitControls enablePan={false} enableZoom={false} maxPolarAngle={Math.PI} minPolarAngle={0} />
                    </Canvas>
                </Suspense>
            </div>
        </div>
    )
}