import React, {useState, useEffect, useRef} from 'react'
import { Canvas } from '@react-three/fiber'
import { OrbitControls } from '@react-three/drei'
import { useSpring } from '@react-spring/core'
import { a } from '@react-spring/web'
import Overlay from './Overlay'
import Testing from './Testing'
import Header from './Header'
import Scene from './Scene'
import AboutMe from './AboutMe'
import Experience from './Experience'
import Loader from './Loader'



export default function App() {

  // This spring controls the background and the svg fill (text color)
  const [{ background, fill }, set] = useSpring({ background: '#f0f0f0', fill: '#202020' }, [])
  const [backgroundC, setBackgroundC] = useState(background.animation.to)
  const [Loading, setLoading] = useState(false)

  useEffect(() => {
    setLoading(true)
    setTimeout(() => {
      setLoading(false);
    }, 6000);
  }, [])
  return (
    <>
      {
        Loading ?
          (
            <Loader />
          )
        :
          (
            <div className='app-main-container' id="container">
              <Header backgroundC={background} />
              <div className='pages'>
                <a.main style={{ background }}>
                  <Canvas className="canvas" dpr={[1, 2]}>
                    <Scene setBg={set} fill={fill} setBackgroundC={setBackgroundC} />
                    {/* <OrbitControls enablePan={false} enableZoom={false} maxPolarAngle={Math.PI / 2} minPolarAngle={Math.PI / 2} /> */}
                  </Canvas>
                  {/* <Overlay fill={fill} /> */}
                  {/* <Testing /> */}
                </a.main>
              </div>
              <div className='pages'>
                <a.main style={{ background }}>
                  <AboutMe backgroundC={background} />
                </a.main>
              </div>
              <div className='pages'>
                <a.main style={{ background }}>
                  <Experience backgroundC={background} />
                </a.main>
              </div>
            </div>
          )
      }
    </>
  )
}
